.user_cart{
display: flex;

position:relative;
/* border: 2px solid red; */
}

.cart_title{
padding: 0  2rem;
min-height: 150px;
display: flex;
flex-direction: column;
justify-content: center;
/* border: 1px  solid rgba(255, 255, 0, 0.295); */
}

.cart_container{
/* border: 5px solid #fff; */
color: #fff;
display: flex;
flex-direction: column;
padding: .5rem;
/* border: 1px solid; */
}



.product_info{
display: flex;
text-shadow: 0px 0px 10px #40E1F7;
color: #38D6BE;
/* border: 10px  solid rgba(255, 255, 0, 0.295); */
/* border-top: 1px  solid transparent; */
border-bottom: 1px solid rgba(255, 255, 255, 0.199);
gap: clamp(2rem, 1rem + 2vw, 3rem );
padding: 2rem;

}


/* .image_box{


border: 3px solid red; 
} */
.product_img{
width:clamp(200px,100px + 10vw , 230px);
max-width: 230px;

}

.product_details{
display: flex;
flex-direction: column;
gap:clamp(1rem,1rem + 1vw , 2rem);
}

.qty{
height: 30px;
width: 50px;
border-radius: 20px;
background-color: #38d6be48;
display: flex;
align-items: center;
position: relative;
}

.qty_btn{
/* border: 2px solid green; */
overflow: hidden;

}

.qty_input{
color: #38D6BE;
border: none;
background:none;
font-family: var(--global-fonts);
text-align: center;
text-shadow: 0px 0px 10px #40E1F7;
width: 100%;
height: 100%;
}

.qty_controls {
display: flex;
gap: 1rem;
position: absolute;
right: -50px;
}

.crud{
display: flex;
gap: 1.2rem;
}
.crud:first-child{
display: none;
}

.crud_icon{
opacity: .7;
color: #ED2F0E;
text-shadow: 0px 0px 10px #f56f576c;
font-size: 1.8rem;
background:none;
border: 1px solid transparent
}
.crud_icon:hover{
border: 1 solid rgba(255, 255, 255, 0.082);
}


.price_details{
display: flex;
flex-direction: column;
justify-content: end;
align-items: flex-end;
gap: 2rem;
font-size: clamp(1.8rem, 1rem + 1vw , 2rem);
}

.proceed_buy_box{
position: relative;
/* border: 4px solid blue; */
color: #fff;
}

.proceed{
display: flex;
flex-direction: column;
gap: 1rem;
padding: 1rem;
position: fixed;
}


@media (max-width: 768px ) {
.product_info,.user_cart{
flex-direction: column;}
.proceed{
position: relative ;}

}