
.loader{
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 99
}

.circle_large,.circle_medium,.circle_small{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 10px #89f1f6;
    border: 4px solid #85e3ee;
    border-radius: 50%;
}
.circle_large{
    border-right: 4px solid transparent;
    padding: 2px;
    height: 55px;
    width: 55px;
    
    animation: spin 2s .1s linear infinite forwards;
}
.circle_medium{
    animation: spin 1.5s .2s linear infinite forwards;
    border-top: 4px solid transparent;
    border-bottom: 4px solid transparent;
    height: 48px;
    width: 48px;
 
}

.circle_small{
    animation: spin 1s .3s linear infinite forwards;
    border-bottom: 4px solid transparent;
    border-left: 4px solid transparent;
    height: 30px;
    width: 30px;
  
}


@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}