@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500&display=swap&family=Alfa+Slab+One&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
--global-fonts:'Montserrat';
--cyber-font:'Alfa Slab One';
--box-padding: 1.3rem;
--container-padding:2rem;


--btn--dark:rgb(2, 2, 77);
--btn--light:purple;


}

html{
  font-size: 62.5%;
}
body{
  background-color: #000;
  font-size: 1.6rem;
  font-family: var(--global-fonts);
  
 
}


.btn__dark,.btn__light,.btn__cart{
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
padding: 10px 15px;
font-size: 1.4rem;
font-family: var(--global-fonts);
font-weight: 500;
max-width: 130px;
max-height: 40px;
cursor: pointer;
color: rgba(255, 255, 255, 0.822);
}

.btn__light{
  background-color: #2B9FD5;
}

.btn__dark{  
position: relative;
/* background:linear-gradient(45deg,,#630587 ); */
border: 1px solid #5a2de33b  ;
}
.btn__cart{
background: none ;
color: #E3E019;
border: 1px solid #D6B022 ;
max-width: 150px ;
}

.neon-hover{
position: relative;

}

.neon-hover::before,.btn__dark::before{
content: '';
position: absolute;
inset: -3px -3px ;
border: 4px solid transparent;}

.neon-hover:hover::before{
border-color: #D6B022;
filter: blur(4px);
}

.btn__dark:hover::before{
  border-color: #2b9fd552;
  filter: blur(4px);
}
li{
  list-style: none;

}

a{
  text-decoration: none;
  color: #2B9FD5;
  cursor: pointer;
}

img{
  max-width: 100%;

}
