@keyframes spin {
0%{
  transform: rotate(0deg);
  }
100%{
  transform: rotate(360deg);
}
}


.prod_title{
font-size: clamp(2rem, 1rem + 3vw ,2.8rem);
}

.prod_subtitle{
  font-size: clamp(1.6rem , 1rem + 1vw , 2rem);
}

.font-14 {
  font-size: 1.4rem;
}

.f-18{
  font-size: 1.8rem;
}

.f-20{ font-size: 2rem;}


.normal-neon-font {
  text-shadow: 0px 0px 10px #89f1f6;
  color: #85e3ee;
}
.pos-r{
  position: relative;
}

.pad-20{
  padding: 2rem;
}


.border-r-20{
  border-radius: 2rem;
}

hr{
  filter: blur(2px);
  border-color: #85e3ee44;
  width: 100%;
  margin-top: 4rem;
  }

.title_neon,.title_neon-2r,.title_small{
font-size: clamp(2rem, 1rem + 3vw ,2.8rem);
text-shadow: 0px 0px 10px #89f1f6;
color: #85e3ee;
  }



.title_neon-2r{
font-size: 2rem;}



.title_small{
  font-size: 1.5rem;
}
.yellow-theme{
  text-shadow: 0px 0px 10px #E3E019;
color: #D6B022;
}

.opc-8:hover{
opacity: .8
}
