.main_container{
/* border: 3px solid red; */
color: rgba(255, 255, 255, 0.288);
display: flex;
flex-direction: column;
margin-inline: auto;
gap: 2rem;
max-width: 1080px;
padding: 1.2rem;
}



.user_stats {
width: 100%;
flex-grow: 1;
padding: 2rem;
border: 1px solid rgba(255, 255, 255, 0.226) ;
border-radius: 20px;
}


.user_data{
display: grid;
grid-template-columns: 1fr;
width: 100%;
grid-gap: 2rem;}








@media (min-width:841px){
.main_container{
flex-direction: row;
}

.user_stats {
max-width: 300px;
flex-grow: 1;
display: flex;
flex-direction: column;
gap: 1.2rem;
}

.user_data{
border-color: #ff0000;
}  
.image_box{
justify-content: space-between;}       
}

@media (min-width:1080px){
.user_data{
grid-template-columns: 1fr 1fr;
}
.user_boughts{ grid-column: span 2;
}
}




.details{
display: flex;
flex-direction: column;
gap: 2rem;
border-bottom: 1px solid rgba(128, 128, 128, 0.274);
padding-bottom: 1.5rem;
}
.image_box{
display: flex;
gap: 2rem;
padding: 1.2rem 0;
}

.user_image{
max-width: 80px;
border-radius: 50%;
overflow: hidden;  
}

.user_img{
object-fit: cover;
height: 100%;
}

.name{
display: block;
}

.contacts{
display: flex;
flex-direction: column;
justify-content: center;
gap: 1rem;
padding-top: 1.2rem;
}


.contact_item i{
margin-right: 1rem;
color: blueviolet;
}

.short,.large{
border: 1px solid rgba(255, 255, 255, 0.336);
border-radius: 2rem;
}