
.products__item{
background-color: #131313;
justify-self: center;
padding: 2rem;
width: 100%;
max-width: 284px;
min-width: 284px;
max-height: 45rem;
cursor: auto;
position: relative;
}

.products__item::before{
content: '';
position: absolute;
inset:  -1px;
background: linear-gradient(45deg,#5A2DE3,#630587,#3AAFC1,#111C37);
z-index: -1;
opacity: .7;
filter: blur(5px);
}

.product__image{
width: 100%;
/* max-width: 284px; */
min-height: 220px;


}
.product__image .product_img{
  width: 100%;
  height: 100%;
object-fit: contain;
  
}
.product__details{
width: 100%;
display: flex;
flex-direction: column;
padding: 1rem 2px;
gap: 2rem;
}

.product__details__title{
line-height: 1;

}

.product__details__title span{
font-size: 1.2rem;
}

/* .product__details_price{

} */


.actions {
display: flex;
gap: 1rem;
justify-content: center;
z-index: 99;
min-height: 30px;
}

.btn_icon{

background: none;
display: flex;
align-items: center;
color: #3AAFC1;
text-shadow: 0px 0px 12px #3AAFC1;}
.btn_icon i {
font-size: 2rem;
}

