.login{
height: calc(80vh - 20px);
display: flex;
padding: var(--container-padding);
justify-content: center;
align-items: center;
flex-direction: column;

}

.login__section{
background:rgba(0, 0, 0, 0.178);
border: 1px solid rgba(255, 255, 255, 0.26);
color:rgba(255, 255, 255, 0.555) ;
display: flex;
min-height: 400px;
flex-direction: column;
padding: var(--container-padding);
position: relative;

}


.login__title{ 
color: #FFF;
display: flex;
flex-direction: column;
gap: 2rem;
text-align: center;
width: 100%;
max-width: 300px;
margin-top: 2rem;
padding: 1.2rem;
}

.login__title p{
font-size: 1.3rem;
color: rgba(255, 255, 255, 0.555);
}


.login__container{
    margin-top: 4rem;
width: 300px;
padding: var(--container-padding);
display: flex;
align-items:  center;
flex-direction: column;
}

.form_login__authentication{
width: 100%;
display: flex;
flex-direction: column;
align-items: center;
gap:2rem;
}
.form_login__item{
display: flex;
flex-direction: column;
width: 100%;
gap: 1rem;

}
.form_login__input{
padding: 10px;
width: 100%;
border: none;
outline: none;
background-color: #333;
color: #FFF;
font-family: var(--global-fonts);
}


.form_login__btn{
position: relative;
border: none;
background-color: var(--btn-dark);
border: 1px solid rgba(255, 255, 255, 0.363);
color: rgba(255, 255, 255, 0.616);
}

.form_login__btn::before{
content: '';
position: absolute;
height: 100%;
width: 100%;
top: -5px;
left: -5px;
border: 5px solid transparent;
transition: .3s ease-in-out;


}

.form_login__btn:hover::before{
transition: .2s ease-in;
border-color: #c04a9e;
filter: blur(5px);
}




.login__message_error{
font-size: 1.3rem;
background-color: #c04a9f38;
color: rgba(255, 255, 255, 0.63);
margin-top: 1rem;
padding: 2px 5px;
}

.createAccount{
    position: absolute;
    inset:  auto 1rem 1rem auto;
    font-size: 1.2rem;
    text-shadow: 1px 1px 1px 2px rgb(62, 60, 216);
}