.search_container{
display: flex;
/* flex-direction: column; */
/* border: 2px solid red; */
padding: 2rem 0;
color: #fff;
position: relative;
min-height: 100vh;
}

.filter{
display: flex;
flex-direction: column;
gap: clamp(1.2rem, .5rem + 10vw , 2rem);
padding: .5rem;
position: fixed;
top: 200px;
z-index: 99;
min-width: 50px;
border: 1px solid #ffffff28;
background-color: #00000028;
margin-left: .5rem;
}
.filter:hover{
    background-color: #000;
}

