.filter_item{
display: flex;
flex-direction: column;
gap: 1rem;
color: #FFF;
font-weight: lighter;
font-size: 1.3rem;
/* border: 2px solid; */
padding: 1rem;
cursor: pointer;
}

.filter_item_title{
font-weight: bold;
font-size: 1.6rem;
border-bottom: 1px solid rgba(228, 228, 228, 0.236);
max-width: 100px;
padding: 1rem .3rem;
}

.filter_item label{
display: flex;
gap: .5rem
}

.filter_opt{
font-weight: 500;
cursor: pointer;}

.filter_opt:hover{
opacity: .8;
}
.search_form{
display: flex;
gap: .2rem;
align-items: center;

}

.search_form input{
max-width: 55px;
padding: 4px;
border-radius: 5px;
border: none;
font-size: 1rem;
outline: none;
font-family: var(--global-fonts);
font-weight: 500;
}

.search_form button{
background: none;
border: none;
cursor: pointer;
}




.products_container{
display: grid;
grid-template-columns: repeat(auto-fit, minmax(284px, 1fr));
width: 100%;
gap: 2rem;
margin-top: 5rem;
}

@media (max-width:560px){
    .toggle{display:block}
}