.register{
min-height: 80vh;
display: flex;
padding: var(--container-padding);
justify-content: center;
align-items: center;
flex-direction: column;
}

.register__section{
background:rgba(0, 0, 0, 0.178);
border: 1px solid rgba(255, 255, 255, 0.26);
color:rgba(255, 255, 255, 0.555) ;
display: flex;
flex-direction: column;
padding: var(--container-padding);

}


.register__title{
color: #FFF;
display: flex;
flex-direction: column;
gap: 2rem;
max-width: 300px;

padding: 1.2rem;
text-align: center;
width: 100%;
}

.register__title p{
font-size: 1.3rem;
color: rgba(255, 255, 255, 0.555);
}


.register__container{


width: 300px;
padding: var(--container-padding);
display: flex;
align-items:  center;
flex-direction: column;
}

.form_register__authentication{
margin-top: 4rem;
width: 100%;
display: flex;
flex-direction: column;
align-items: center;
gap: 3rem;
}
.form_register__item{
display: flex;
border: 1px solid rgba(255, 255, 255, 0.219);
flex-direction: column;
width: 100%;
gap: 1rem;
position: relative;
}

.register__dataName{
position: absolute;
top: 10px;
left: 30px;
transition: .2s;
z-index: 2;
}

.icon{
position: absolute;
left: 10px;
top: 10px;
font-size: 1.4rem;
color: #c04a9f8a;
z-index: 2
}


.form_register__input{
padding: 10px 30px;
width: 100%;
border: none;
outline: none;
background-color: #333;
color: #FFF;
font-family: var(--global-fonts);
z-index:1;
}

.form_register__item::before{
border: 4px solid transparent;
content: '';
height: 100%;
width: 100%;
position: absolute;
top: -4px;
left: -4px;
}

.form_register__item:has(input:focus)::before,.form_register__item:has(input:valid)::before{
border-color: #c04a9f67;
filter:blur(2px)
}


.form_register__item:has(input:focus) .register__dataName , .form_register__item:has(input:valid) .register__dataName{
top: -25px;
left: -5px;
font-size: 1.4rem;
}



.form_register__btn{
position: relative;
border: none;
background-color: var(--btn-dark);
border: 1px solid rgba(255, 255, 255, 0.363);
color: rgba(255, 255, 255, 0.616);
}

.form_register__btn::before{
content: '';
border: 5px solid transparent;
position: absolute;
height: 100%;
width: 100%;
top: -5px;
left: -5px;
transition: .3s ease-in-out;


}

.form_register__btn:hover::before{
transition: .2s ease-in;
border-color: #c04a9e;
filter: blur(5px);
}




.register__message_error{
font-size: 1.3rem;
background-color:#c04a9f42;
color: rgb(255, 255, 255);
margin-top: 1rem;
padding: 2px;
}