.header{
/* background:radial-gradient( #151654,#1D2B78,rgb(10, 9, 85)); */

display: flex;

}

.header__container{
width: 100%;
margin-inline: auto;}

.row1,.row2{
display: flex;
justify-content: center;
align-items: center;
padding: calc(var(--container-padding) - 1rem);
position: relative;}

.row1__content{
display: flex;
align-items: center;
width: 100%;
max-width: 1024px;}

/* container que vai servir como borda do form */
.form_container{
margin-left: clamp(1.5rem, 1rem + 5vw , 10rem);
width: 100%;
padding: 2px;
background: linear-gradient(45deg,#5A2DE3,#630587,#3AAFC1,#111C37);
opacity: .6;
max-width: 500px;
transition: all .3s;
}

.form_container:has(input[type ='text']:focus) {
opacity: 1;
}

/* .search__form{

} */

.search__container{
display: flex;
position: relative;
align-items: center;}

.search__container::before{
content: '';
height: 100%;
width: 100%;
position: absolute;
border: 2px solid transparent;
top: -2px;
left: -2px;
z-index: 0;
transition: all .5s ease-in-out;}

.search__container:has(input[type=text]:focus-visible)::before{
filter: blur(2px);
border-color: #1B8B8F;
z-index: 1;}

.search__container input[type="text"]{
    border:none;
    background: #131313;
    font-family: var(--global-fonts);
    outline: none;
    padding: 10px;
    color: #fff;
    width: 100%;
    z-index: 1;}

.search__btn{
position: absolute;
right: 0;
height: 100%;
border: none;
cursor: pointer;
width: 50px;
z-index: 1;
background: #131313;
color: #38bffd;}


.addCart__btn {
background: none;
font-size: 2rem;
text-shadow: 0px 0px 10px #89f1f6;
color: #85e3ee;
position: relative;}

.addCart__btn:hover{
color: #89f1f6;}

.cart_qty{
    position: absolute;
    top: 2px;
    right: 2px;
    font-size: .8rem;
    height: 13px;
    width: 13px;
    display: flex ;
    justify-content: center;
    align-items: center;
    border: 1px solid #38bffd41;
    border-radius: 100px;
}

.user__btn{
border: 1px solid #1b8b8f4b;
border-radius: 50%;
cursor: pointer;
text-shadow: 0px 0px 10px #89f1f6;
color: #85e3ee;
display: flex;
align-items: center;
justify-content: center;
font-size: 1.4rem;
margin-left: 10px;
min-height: 50px;
min-width: 50px;
position: relative;}

.user__btn:hover  .user_submenu {
display: flex;}

.user_icon{
font-size: 2rem;

}

.user_submenu{
background-color: #131313b7;
text-shadow: 1px 1px 8px #794a8b ;
border-radius: 2px;
display: none; 
flex-direction: column;
cursor: auto;
padding: 2rem 1rem;
position: absolute;
top:calc( 100% - 8px);
right:calc(100% - 55px);
width: 200px;
gap: 1.5rem;
z-index: 99;}

.user_submenu::before{
background-color: #131313b7;
content: '';
height: 20px;
width: 20px;
position: absolute;
top: -6px;
right: 15px;
transform: rotate(45deg);}

.user_submenu_visible {
display: flex;
flex-direction: column;
gap: 1.2rem;}

.user_submenu_item {
border: 1px solid transparent;
padding: 1.2rem;
position: relative;
background: none;
z-index: 0;
cursor: pointer;
display: flex;
justify-content: space-between;}

.user_submenu_item::before{
content: '';
border-right: 4px solid #4d0a5e;
border-top: 4px solid transparent;
border-bottom: 4px solid transparent;
border-left: 4px solid transparent;
height: 100%;
width: 0%;
position: absolute;
top: -4px;
left: -4px;
transition: .1s width;   
z-index: -1;}

.user_submenu_item:hover{
transition:.1s .2s border;
border-color: #2B9FD5;}

.user_submenu_item:hover::before{
border-color: purple;
filter: blur(5px);
width: 100%;
transition: .3s width, .1s .2s border,.1s .2s filter;}

.row2{
height: 66px;
border-top: 1px solid rgba(255, 255, 255, 0.103);}

.row2__content{
width: 100%;
max-width: 1024px;
display: flex;
justify-content: space-between;}


.navbar{
display: flex;
justify-content: space-between;
align-items: center;
width:100%;
}
.menu_items{
    position: relative;
   
}


.category_submenu{
    background-color: rgb(3, 3, 3);
    border: 1px solid #2B9FD5;
    box-shadow: 1px 1px 10px 0px #38bffd;
    display: none;
    font-size: 1.;
    justify-content: space-around;
    gap: 2rem;
    min-width: 250px;
    padding: 2rem;
    position: absolute;
    left: 0;
    z-index: 2;
   
}

.menu_category:hover .category_submenu{
     display: flex;}

.mainMenu,.loginOrRegister{
display: flex;
gap: 3rem;}

.mainMenu{
    text-shadow: 0px 0px 10px #89f1f6;
    color: #85e3ee;
}

.toggle{display: none;}

.rotate{

    font-size: 1.2rem;
   position: absolute;
   right: -15px;
   top: 5px;
   transition: .2s linear;

}
.menu_category:hover .rotate {
transform: rotate(180deg);

}

@media (max-width:900px){
.navbar { 
display: none;
flex-direction: column-reverse;
justify-content: flex-end;
gap: 3rem;
background-color: #000;
padding: 1.2rem;
position: absolute;
top: 100%;
left: 0;
min-height: 400px;
width: 100%;
z-index:2;}
.mainMenu{
flex-direction: column;
align-self: flex-start;
}
.menu_toggle {
display: flex;
justify-content: center;
align-items:center;
min-height: 30px;
width: 30px;
position: relative;}
.menu_toggle_row,.menu_toggle_row::before,.menu_toggle_row::after{
content: '';
height: 3px;
width: 100%;
background-color:#38bffd ;
border-radius: 1000px;
box-shadow: 0px 0px 2px 2px #1b8b8f98;
position: absolute;
transition:  margin .2s  linear , transform .3s linear,background-color .1s .2s linear}

.menu_toggle_row::before{
margin-top: -10px; }
.menu_toggle_row::after{
margin-top: 10px; }

.menu_toggle:has(input[type=checkbox]:checked) ~ .navbar{display: flex;}
.menu_toggle:has(input[type=checkbox]:checked)  .menu_toggle_row{
    background: none; 
    transition:0s ;
    box-shadow: none;}
.menu_toggle:has(input[type=checkbox]:checked)  .menu_toggle_row::before{
    margin-top: 0; 
    opacity: 1; transform: rotate(45deg);}
.menu_toggle:has(input[type=checkbox]:checked)  .menu_toggle_row::after{
    margin-top: 0; 
    opacity: 1;transform: rotate(-45deg)}
}






@keyframes button-effect{
0%{
top: 0;
left: 0;
}   
100%{
top: 0;
left: -100%;
}}