.mainContainer{
    display: block;
    color: aqua;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    padding: 2rem;
}

.container{
    width: 100%;
    max-width: 50rem;
}

.purchase_form{
    /* border: 4px solid red; */
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    margin: auto;
    padding: 1.2rem;
}

.form_item{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
.check{
    display: flex;
    gap: 2rem;
}

.form_item input{
    
    background: none;
    border: 1px solid #ffffff3f;
    border-radius: .5rem;
    box-shadow: 0px 0px 100px #89f1f671;
    color: aqua;
    outline: none;
    padding: .9rem;
}

.submit{
    background: none;
    border: none;
   font-size: 5rem;
   color: #000;
   text-shadow: 0px 0px 8px #89f1f6;
    margin: 3rem auto;

    cursor: pointer;
}
.submit:hover{
    text-shadow: 0px 0px 12px #89f1f6b9;
  }