footer{
    background-color: #000;
    border-top: 1px solid #ed9dc82d;
    color: #85e3ee;
    display: flex;
    flex-direction: column;
    padding:  8rem clamp(4rem,3rem + 4vw , 8rem);
    align-items: center;
    font-size: clamp(1.1rem, .8rem + 1vw, 1.4rem);
    gap: 2rem;
    max-width: 70rem;
   
   text-align: center;
   margin: auto;
    text-shadow: 0px 0px 10px #89f1f6;

}

.logo_container{
    display: flex;
    align-items: center;
    justify-content: center;

}



.contacts{
    display: flex;
    justify-content: center;
    gap: 2rem;
    text-shadow: 0px 0px 10px #89f1f6;
    color: #85e3ee;
    font-size: 1.8rem;
}

.contact_item{
    cursor: pointer;
}