
.edit_profile{

display: grid;
grid-template-columns: repeat(4 , 1fr);
grid-template-areas: 
'user user user user'
'ctrl display display display';
grid-gap: 0 1.2rem;
position: relative;
margin-bottom: clamp(1rem, 1rem + 15vw,30rem);

}

.user{
/* border: 3px solid green; */
background: linear-gradient(to bottom, rgb(170, 168, 168) 80%, #fff );
display: flex;
align-items: center;
grid-area: user;
padding-left: 5rem;
width: 100%;
min-height: 285px;

}

.user_details{ 
display: flex;
flex-direction: column;
gap: 1.2rem;
text-align: center;
font-size: 2rem;
font-weight: 500;
}

.image_box{
border-radius: 20px;
cursor: pointer;
max-height: 110px;
max-width: 120px;
overflow: hidden;
position: relative;  
}

.image_box:hover  .change_pic{
display: flex;
}

.change_pic{
background-color: rgba(0, 0, 0, 0.726);
color: rgb(223, 222, 222);
display: none;
flex-direction: column;
align-items: center;
justify-content: center;
font-size: 1.3rem;
position: absolute;
height: 100%;
width: 100%;
}
.cam_icon{
font-size: 4rem;}

.back_icon{
font-size: 1.5rem;
position: absolute;
transform: translateX(5px);
cursor: pointer;
    
}


.info_controller{
color: #fff;
display: flex;
flex-direction: column;
gap: 2rem;
grid-area: ctrl;
padding: 2rem;
/* border: 2px solid red; */
height: 100%;
}

/* 
esse aqui nao  o mobile version  
*/
.ctrl_btn{
cursor: pointer;
display: flex;
flex-direction: row;
gap: 1rem;
}

.ctrl_btn:hover .show_option{
opacity: 1;
}

    

.info_display{

background-color: #000;
border-radius: 20px;
box-shadow: 1px 1px 15px 2px rgba(102, 51, 153, 0.26);
color: #fff;
display: flex;
flex-direction: column;
min-height: 400px;
grid-area: display;
padding: 2rem;
position: absolute;
inset: -50px 20px 0 0 ;

}





@media(max-width:768px){
.edit_profile{
    grid-template-columns: 1fr;
grid-template-areas: 
'user'
'ctrl'
'display';
}
.user{
    justify-content: center;
    padding: 0;
}
.info_controller{
flex-direction: row;
justify-content: space-around;
border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.info_display{
    position: static;
}
.ctrl_btn{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
gap: 1rem;
}

.show_option{
    opacity: 0; 
    font-size: 1.4rem;}

}

