.offers{

  position: relative;
}

.image_box{
display: block;


}
.image_box image{
aspect-ratio: 2/1;
}

.slide_controls{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: clamp(4rem,2rem + 3vw, 5.5rem);
  position: absolute;
  top: 45%;
  left: 0;
  z-index: 1;
  width: 100%;
  overflow: hidden;
}

.btn_manual{

height: 100%;
width: clamp(4rem,2rem + 3vw, 5.5rem);
padding:clamp(.3rem , .1rem + .8vw, .8rem);
border: 2px solid;
display: flex;
align-items: center;
position: absolute;
border-radius: 50%;
cursor: pointer;
background-color: #000;
}

.btn_manual:hover{
  text-shadow: 0px 0px 10px #89f1f6;
color: #85e3ee;
}

.btn_left{
  left: -20px;
  justify-content: flex-end;
}
.btn_right{
  right: -20px;
  justify-content: flex-start;
}

.products{
width: 100%;
padding: 2rem;
display: flex;
flex-direction: column;
gap: 2rem;
margin-top: 100px;}

.products__container{
display: grid;
grid-template-columns: repeat(auto-fit,minmax( 284px, 1fr));
grid-gap: 2rem;
padding: 1rem;
justify-content: center;
align-items: center;
width: 100%;

}


.actions{
  display: flex;
  gap: 1rem;
  justify-content: center;}


  @keyframes color-change {
    0%{
      background-color: rgb(0, 17, 175);
    }

    100%{
      background-color: blueviolet;
    
    }

  }