.about{
padding: 2rem;


}


.discription_container{
background: none;
/* border: 1px solid #ab79d6; */
display: grid;


grid-template-columns: 1fr 1fr;
margin-top: 10px; /* temporario */
min-height: 400px;
padding: 2rem;
position: relative;




}

/*  */
.discription_content{
/* border: 3px solid green; */
display: flex;
flex-direction: column;
justify-content: center;
justify-self: center;
max-width: 500px;
text-align: center;
gap: 3rem;
letter-spacing: 2px;
line-height: 1.4;
padding: 20px;
color: #fff;
}

.discription_content h3{
font-size: 4rem;
color: #ab79d6;

}

.einstein_container{
display: flex;
justify-content: center;
align-items: center;

}

/*  */

.einstein_container_image{
/* border: 2px solid red; */
aspect-ratio: 1 / 1.1;
border-radius: 0px 0px 160px 160px ;
display: grid;
align-items: flex-end;
justify-content: center;
max-width: clamp(240px, 240px + 10vw , 320px);
overflow: hidden;
position: relative;
z-index: 0;
}

.bg_circle{
position: absolute;
inset: auto 0 0 ;
width: 100%;
aspect-ratio: 1/1;
border-radius: 50%;
margin-inline: auto;
background-color: rgba(128, 0, 128, 0.37);
}

.einstein_img{
z-index: 99;
transition: transform 1s  linear;
}

.discription_container:hover .einstein_img{
transition: transform .5s  linear;
transform: translate(-8px,-8px) scale(1.05);
}


.grid_cards{
display: grid;
/* border: 1px solid green; */
grid-auto-columns: 1fr;
grid-template-areas: 
'one one one one two two'
'three three four four four four'
'five   five five six seven  seven'
'eight eight eight nine nine nine ';
grid-gap: 2rem;
}

.grid_item{
align-content: center;
/* border: 3px solid rgba(0, 140, 255, 0.24); */
background: #111C37;
color: #85e3ee;
display: flex;
flex-direction: column;
font-size: 1.4rem;
line-height: 1.5;
justify-content: center;
min-height: 80px;
position: relative;
text-align: center;
text-shadow: 0px 0px 10px #89f1f6;
transform: skew(5deg);

}




